                <ProjectCard image={BruinDining} name="Bruin Dining" description="Bruin Dining is an iOS Widget that displays in a list of the dining halls that are currently open at UCLA. No more walking 15 minutes to your favorite dining place just to be met by locked doors ;)" links={{"TestFlight": "https://testflight.apple.com/join/PigzAQ87"}}/>
