const ProjectsSection = (props) => {

    const [expanded, setExpanded] = useState(true)
    return (
        <div class={"ProjectsSection"}>
            <button onClick={()=>setExpanded(!expanded)}><h4>{props.title + "\t\t"}<Chevron className="chevron" direction="up" reversed={expanded}/></h4></button>
            <div id={"ProjectList"+props.tag} class={"CardGrid ProjectsGrid" + (expanded ? " Expanded":"")}>
                {props.children}
            </div>
        </div>
    )
}

const ProjectCard = (props) => {
    return (
        <div class="Card ProjectCard">
            <img class="ProjectImage" src={props.image} alt=""/>
            <h4>{props.name}</h4>
            <p>{props.description}</p>
            <ProjectLinks links={props.links}/>
        </div>
    )
}

const ProjectLinks = (props) => {

    const GitHubPage = ("GitHub" in props.links ? (<a href={props.links["GitHub"]}><img class="LinkIcon" src={GitHubIcon} alt="GitHub"/></a>):<></>)

    const TestFlightPage = ("TestFlight" in props.links ? (<a href={props.links["TestFlight"]}><img class="LinkIcon" src={TestFlightIcon} alt="TestFlight"/></a>):<></>)

    return (
        <div class="ExternalLinks">
            {GitHubPage}
            {TestFlightPage}
        </div>
    )
}


export default Projects